/* ==========================================================================
   WooCommerce Buttons styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
	
	a.button,
	a.added_to_cart,
	input[type="submit"],
	button[type="submit"]:not(.edgtf-search-submit),
	.wc-forward:not(.added_to_cart):not(.checkout-button) {
		@include edgtfButtonDefaultStyle();
		@include edgtfButtonClipPathMain();
		padding: 13px 51px;
		cursor: pointer;
		z-index: 3;
		overflow: hidden;
	}
	
	a.button,
	a.added_to_cart {
		@include edgtfButtonOutlineColor();
		@include edgtfButtonClipPathLines();

		&:hover {
			@include edgtfButtonOutlineHoverColor();
		}
	}
	
	input[type="submit"],
	button[type="submit"]:not(.edgtf-search-submit),
	.wc-forward:not(.added_to_cart) {
		@include edgtfButtonSolidColor();
		
		&:hover {
			@include edgtfButtonSolidHoverColor();
		}
	}
	
	.wc-forward:not(.added_to_cart) {
		
		&:before,
		&:after {
			display: none;
		}
	}
	
	a.product_type_variable {
		padding: 13px 36px;
	}
	
	a.button.added {
		display: none;
	}
}

.woocommerce-page {

	&.edgtf-edge,
	&.edgtf-ms-explorer {

		.edgtf-content {

			a.button,
			a.added_to_cart {

				&:before,
				&:after {
					display: none;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Buttons styles - end
   ========================================================================== */