/* ==========================================================================
   WooCommerce Result and Ordering styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {

    .woocommerce-result-count,
    .woocommerce-ordering {
        display: inline;
        width: 50%;
        margin: 0 0 41px;
        float: left;
    }

    .woocommerce-result-count {
        line-height: #{$sselect-input-height}px;
    }

    .woocommerce-ordering {
        text-align: right;

        .orderby {
            width: auto;
            min-width: 250px;
            margin: 0;
            text-align: initial;
        }

        .select2 {
            margin-bottom: 0;
            text-align: initial;
        }
    
        .select2-container--default {
            text-align: left;
            
            .select2-selection--single {
                @include edgtfButtonClipPathOtherSide();
            }
        }
    }
}

/* ==========================================================================
   WooCommerce Result and Ordering styles - end
   ========================================================================== */