/* ==========================================================================
   WooCommerce Message styles - begin
   ========================================================================== */
.edgtf-woocommerce-page {
	
	.woocommerce-message,
	.woocommerce-info,
	.woocommerce-error {
		position: relative;
		display: block;
		padding: 34px 30px;
		margin: 0 0 30px;
		list-style: none;
		background-color: $additional-background-color;
		border: 1px solid $default-border-color;
		@include edgtfHeadingStyle(h5);
		line-height: 38px;
		box-shadow: 0 10px 27px 0 rgba(14, 8, 18, 0.8);
		text-shadow: 0 10px 27px rgba(14, 8, 18, 0.8);
		
		> a {
			color: $default-text-color;
			
			&:hover {
				color: $default-heading-color;
			}
		}
		
		a {
			float: right;
		}
	}
	
	.woocommerce-message,
	.woocommerce-error {
		
		a {
			bottom: 13px;
			@include edgtfButtonOutlineColor(!important);
			
			&:hover {
				@include edgtfButtonOutlineHoverColor(!important);
			}
			
			&:before,
			&:after {
				display: inline-block !important;
			}
		}
	}
	
	.woocommerce-info {
		
		.showcoupon {
			color: $default-text-color;
			
			&:hover {
				color: $default-heading-color;
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Message styles - end
   ========================================================================== */