/* ==========================================================================
   WooCommerce Product List page styles - begin
   ========================================================================== */

.edgtf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells,
.edgtf-content .woocommerce {
	@include edgtfRelativeHolderLayout();
}

.edgtf-pl-main-holder {
	margin: 0 0 22px;
}

.edgtf-woo-pl-info-on-image-hover {
	margin: 0 0 50px;
	
	ul.products,
	ul.related.products {
		
		> .product {
			
			.edgtf-pl-inner {
				
				.edgtf-pl-text {
					opacity: 1;
					background-color: transparent;
					padding: 23px 21px;
					transition: all .2s ease-out;
					
					.edgtf-pl-text-outer {
						text-align: left;
						
						.edgtf-pl-text-inner {
							display: flex;
							justify-content: center;
							align-items: center;
							width: 100%;
							height: 100%;

							.edgtf-pl-text-inner-2 {
								display: flex;
								align-items: flex-start;
								justify-content: flex-end;
								flex-direction: column;
								width: 100%;
								height: 100%;
							}

							.edgtf-pl-btn-inner {
								position: absolute;
								top: 0;
								left: 0;
								width: 100%;
								height: 100%;
								display: flex;
								align-items: center;
								justify-content: center;
							}

							.edgtf-product-categories {
								margin-top: 5px;
								padding-bottom: 0;
							}

							.button {
								display: none;
							}
						}
					}
				}
			}
			
			&:hover {
				
				.edgtf-pl-text {
					background-color: $default-overlay-color;
					transition: all .2s ease-out;
					
					.edgtf-pl-text-outer {
						
						.edgtf-pl-text-inner {

							.edgtf-pl-text-inner-2 {
								opacity: 0;
							}

							.button {
								display: block;
								margin: 0;
							}
						}
					}
				}
			}
		}
	}
}

ul.products {
	list-style: none;
	margin: 0 -15px;
	clear: both;
	
	> .product {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		float: left;
		margin: 0 0 30px;
		padding: 0 15px;
		box-sizing: border-box;
		
		.touch & {
			cursor: pointer;
		}
		
		&:hover {
			
			.edgtf-pl-inner .edgtf-pl-text {
				opacity: 1;
			}
		}
		
		.edgtf-product-list-title {
			margin: 0;
		}
		
		.edgtf-pl-rating-holder {
			@include edgtfRelativeHolderLayout();
			line-height: 1;
			margin: 10px 0 3px;
		}
		
		.price {
			@include edgtfWooCommercePriceStyle();
			@include edgtfHeadingStyle(h4);
			margin: 0;
			display: flex;
			flex-shrink: 1;
		}
		
		.button,
		.added_to_cart {
			margin: 25px 0 0;
			
			.edgtf-woo-pl-info-below-image & {
				margin: 0;
			}
		}
		
		.button.added {
			display: none;
		}
		
		.edgtf-pl-inner {
			@include edgtfRelativeHolderLayout();
			
			.edgtf-pl-image {
				@include edgtfRelativeHolderLayout();
				box-shadow: $default-box-shadow;
				
				img {
					display: block;
					width: 100%;
				}
			}
			
			.edgtf-pl-text {
				@include edgtfAbsoluteHolderLayout();
				padding: 10px;
				opacity: 0;
				background-color: $default-overlay-color;
				box-sizing: border-box;
				transition: opacity .2s ease-out;
			}
			
			.edgtf-pl-text-outer {
				@include edgtfTableLayout();
				text-align: center;
			}
			
			.edgtf-pl-text-inner {
				@include edgtfTableCellLayout();
				
				.edgtf-product-list-title,
				.price,
				.star-rating {
					color: #fff;
				}
				
				.price del {
					color: rgba(#fff, .8);
				}
			}
			
			> a {
				@include edgtfAbsoluteHolderLayout();
			}
		}
		
		.edgtf-pl-text-wrapper {
			@include edgtfRelativeHolderLayout();
			margin: 23px 0 5px;
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
		}
		
		.edgtf-product-categories {
			@include edgtfHeadingStyle(h6);
			padding-bottom: 14px;
			
			a {
				color: $first-main-color;
				
				&:hover {
					color: $default-info-color;
				}
			}
		}
		
		> a {
			@include edgtfAbsoluteHolderLayout();
		}
	}
}

$columns_space_label: ('no', 'tiny', 'small', 'normal', 'medium', 'large');
$columns_space_width: (0, 5, 10, 15, 20, 25);

@for $i from 0 to length($columns_space_label) {
	&.edgtf-woo-#{nth($columns_space_label,$i+1)}-space {
		$column_width: nth($columns_space_width, $i+1);
		$column_width_margin: $column_width * 2;
		
		ul.products {
			@if ($column_width == 0) {
				margin: 0;
			} @else {
				margin: 0 -#{$column_width}px;
			}
			
			> .product {
				padding: 0 #{$column_width}px;
				margin: 0 0 #{$column_width_margin}px;
			}
		}
	}
}

.edgtf-pl-main-holder,
.related.products,
.upsells.products,
.cross-sells {
	
	$product_list_columns: ('columns-3', 'columns-4');
	
	@for $i from 0 to length($product_list_columns) {
		$column_width: 100%/($i+3);
		
		> .products {
			
			> .product {
				
				@media only screen and (min-width: $ipad-landscape-plus-pixel) {
					
					.edgtf-woocommerce-#{nth($product_list_columns,$i+1)} & {
						width: $column_width;
						
						&:nth-child(#{$i+3}n+1) {
							clear: both;
						}
					}
				}
			}
		}
	}
}

.woocommerce {
	
	&.columns-1 {
		
		> .products {
			
			> .product {
				float: none;
				width: 100%;
			}
		}
	}
	
	$product_list_vc_columns: ('columns-2', 'columns-3', 'columns-4', 'columns-5', 'columns-6');
	
	@for $i from 0 to length($product_list_vc_columns) {
		&.#{nth($product_list_vc_columns,$i+1)} {
			$column_vc_width: 100%/($i+2);
			
			> .products {
				
				> .product {
					width: $column_vc_width;
					$product_list_vc_columns_label: #{nth($product_list_vc_columns,$i+1)};
					
					@if $product_list_vc_columns_label == 'columns-5' {
						@media only screen and (min-width: $laptop-landscape-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else if $product_list_vc_columns_label == 'columns-6' {
						@media only screen and (min-width: $laptop-landscape-large-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else if $product_list_vc_columns_label == 'columns-3' {
						@media only screen and (min-width: $ipad-portrait-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else if $product_list_vc_columns_label == 'columns-2' {
						@media only screen and (min-width: $phone-landscape-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					} @else {
						@media only screen and (min-width: $ipad-landscape-plus-pixel) {
							
							&:nth-child(#{$i+2}n+1) {
								clear: both;
							}
						}
					}
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Product List page styles - end
   ========================================================================== */