/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
	$thumb: 80px;
	$inner-padding: 15px; // distance between image and content
	$widget-list-item-padding: 15px; // same as sidebar vars
	
	clear: both;
	
	li {
		position: relative;
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// title
	
	.product-title {
		@include edgtfH5();
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// star rating
	
	li:not(.wc-layered-nav-rating) {
		.star-rating {
			margin-bottom: 7px;
			margin-top: 7px;
			
			span {
				max-width: 100px;
			}
		}
	}
	
	li.wc-layered-nav-rating {
		a {
			text-decoration: none;
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// price
	
	.amount {
		color: inherit;
		
		span { // currency symbol
			color: inherit;
		}
	}
	
	del,
	ins {
		display: block;
		
		// currency symbol
		span {
			color: inherit;
		}
	}
	
	ins {
		text-decoration: none;
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// layered nav
	
	&.widget_layered_nav {
		.chosen {
			a {
				color: $first-main-color;
			}
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// price filter
	
	&.widget_price_filter {
		form {
			margin-top: 20px;
		}
		
		.price_slider_wrapper {
			
			.ui-widget-content {
				position: relative;
				height: 5px;
				background-color: #0d0711;
				
				.ui-slider-handle {
					position: absolute;
					top: -11px;
					height: 26px;
					width: 2px;
					background-color: $first-main-color;
					margin: 0 0 0 -1px;
					cursor: pointer;
					outline: 0;
					z-index: 2;
					border-radius: 0;
					transform: rotate(-45deg);
					transform-origin: 90% 47%;
					
					&:last-child {
						transform-origin: 90% 58%;
					}
				}
				
				.ui-slider-range {
					position: absolute;
					display: block;
					height: 100%;
					background-color: $first-main-color;
					z-index: 1;
					clip-path: polygon(100% 0, 98% 0, 100% 100%, 0 100%, 2% 100%, 0 0);
				}
			}
		}
		
		.price_slider_amount {
			margin: 25px 0 0;
			
			.button {
				float: right;
				padding: 0 !important;
				color: $default-heading-color;
				background-color: transparent;
				border: 0;
				vertical-align: middle;
				line-height: 1.43em;
				clip-path: none;
				
				&:before {
					display: none;
				}
				
				&:after {
					content: "\f30f";
					position: relative;
					display: inline-block;
					font-family: "Ionicons";
					padding-left: 10px;
					font-size: 18px;
					font-weight: 400;
				}
				
				&:hover {
					background-color: transparent;
					color: $first-main-color;
				}
			}
			
			.price_label {
				@include edgtfHeadingStyle(h6);
				line-height: 2em;
				float: left;
			}
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// widgets with images
	
	&.widget_shopping_cart,
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {
		ul {
			li {
				min-height: $thumb;
				position: relative;
				
				a {
					img {
						left: 0;
						max-width: $thumb;
						position: absolute;
						top: $widget-list-item-padding;
						width: 100%;
					}
					
					span {
						box-sizing: border-box;
						display: block;
						padding: 0 0 0 #{$thumb + $inner-padding};
						position: relative;
					}
				}
				
				> *:not(a) {
					clear: both;
					float: left;
					margin: 0 0 0 #{$thumb + $inner-padding};
				}
			}
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// widgets with images and plain text next to image
	
	&.widget_shopping_cart {
		a {
			display: block;
			padding: 0 0 0 #{$thumb + $inner-padding};
			
			@include edgtfH5();
		}
	}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// recent reviews
	
	//&.widget_recent_reviews {
	//    .reviewer {
	//    }
	//}
	
	////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
	// shopping cart
	
	&.widget_shopping_cart {
		.remove {
			font-size: 0;
			font-weight: normal;
			line-height: 1;
			position: absolute;
			right: 0;
			top: 5px;
			text-decoration: none;
			
			&:after {
				content: "\4d";
				display: block;
				font-family: 'ElegantIcons';
				font-size: 15px;
				line-height: 15px;
				position: relative;
			}
		}
		
		.variation {
			@include edgtfRelativeHolderLayout();
			
			> * {
				display: inline-block;
				margin: 0;
				vertical-align: middle;
				
				p {
					margin: 0;
				}
			}
		}
		
		.total {
			margin: 17px 0 13px;
		}
		
		.buttons {
			.button {
				@include edgtfButtonDefaultStyle();
				@include edgtfButtonSize();
				@include edgtfButtonSolidColor();
				
				&:hover {
					@include edgtfButtonSolidHoverColor();
				}
				
				&:first-child {
					float: left;
					padding: 7px 15px;
				}
				
				&:last-child {
					float: right;
					padding: 7px 15px;
				}
			}
		}
	}
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */