/* Woocommerce Product Page shortcode style - begin
========================================================================== */

div.woocommerce {

    > .single-product {
        @include edgtfRelativeHolderLayout();
        clear: both;
        margin: 0 0 20px;

        .images {
		    position: relative;
		    display: inline-block;
		    vertical-align: middle;
		    width: 58%;
		    margin: 0;
		    padding: 0 29px 0 0;
		    float: left;
		    box-sizing: border-box;

		    figure {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0 -10px;

			    a {
				    @include edgtfRelativeHolderLayout();

				    img {
					    position: relative;
					    width: 100%;
					    display: block;
				    }
			    }
		    }

		    .woocommerce-product-gallery__trigger {
				position: absolute;
			    top: 10px;
			    right: 39px;
			    width: 30px;
			    height: 30px;
			    line-height: 30px;
			    text-align: center;
			    z-index: 10;
		    }

		    .woocommerce-product-gallery__image {
			    position: relative;
			    display: inline-block;
			    vertical-align: top;
			    margin: 0;
			    padding: 0 10px;
			    box-sizing: border-box;

			    &:first-child {
				    left: 10px;
				    width: calc(100% - 20px);
				    padding: 0;
			    }

			    .zoomImg {
				    cursor: crosshair;
			    }
		    }

		    &.woocommerce-product-gallery--with-images {

			    .woocommerce-product-gallery__image {

				    &:not(:first-child) {
					    float: left;
					    padding-top: 20px;
				    }
			    }
		    }

		    $thumb_3_columns_label: ('columns-2', 'columns-3', 'columns-4');
		    @for $i from 0 to length($thumb_3_columns_label) {
			    &.woocommerce-product-gallery--#{nth($thumb_3_columns_label, $i+1)} {
				    $column_width: 100%/($i+2);

				    .woocommerce-product-gallery__image {

					    &:not(:first-child) {
						    width: $column_width;
					    }
				    }
			    }
		    }
	    }

        .edgtf-single-product-summary {
            position: relative;
            display: inline-block;
            vertical-align: middle;
            width: 42%;
            padding: 0 0 0 29px;
            float: left;
            box-sizing: border-box;

            .edgtf-single-product-title {
                display: block;
                margin: 0 0 14px;
            }

            .price {
                @include edgtfWooCommercePriceStyle();
                @include edgtfHeadingStyle(h3);
            }

            .woocommerce-product-rating {
                @include edgtfRelativeHolderLayout();
                line-height: 1;
                margin: 18px 0 3px;
            }

            .woocommerce-product-details__short-description {
                @include edgtfRelativeHolderLayout();
                margin: 31px 0 0;

                p {
                    margin: 0;
                }
            }

            .edgtf-single-product-additional-text {
                @include edgtfHeadingStyle(h4);
                padding-top: 57px;
            }

            .product_meta {
                margin: 19px 0 0;

                > span {
                    display: block;
                    margin: 3px 0;
                    font-family: $default-heading-font;
                    font-weight: 700;
                    font-size: 20px;
                    text-transform: uppercase;
                    color: $default-heading-color;

                    a {
                        color: $default-info-color;
                        font-family: $default-text-font;
                        font-size: 15px;
                        text-transform: none;
                        font-weight: 400;

                        &:hover {
                            color: $first-main-color;
                        }
                    }
                }
            }

            .edgtf-woo-social-share-holder {
                @include edgtfRelativeHolderLayout();
                margin: 0;

                > * {
                    display: table-cell;
                    vertical-align: top;
                }

                > span {
                    padding: 0 9px 0 0;
                }
            }

            p.stock.out-of-stock,
            p.stock.in-stock {
                color: $default-heading-color;
                font-weight: bold;
                margin: 18px 0 0;
            }

            form.cart {
                @include edgtfRelativeHolderLayout();
                margin: 50px 0 0;

                .single_add_to_cart_button {
                    @include edgtfButtonClipPathTopRight(!important);
                    padding: 14px 51px !important;

                    @include phone-landscape {
                        padding: 8px 26px !important;
                    }
                }
            }
        }
    
        .woocommerce-tabs {
            @include edgtfRelativeHolderLayout();
            margin: 80px 0 0;
            padding: 0 28px;
            background-color: $additional-background-color;
            box-sizing: border-box;
            @include edgtfBoxShadows();

            ul.tabs {
                @include edgtfRelativeHolderLayout();
                list-style: none;
                margin: 0;
                padding: 0;
                border-bottom: 1px solid #0f1012;
                text-align: center;
                clear: both;

                > li {
                    position: relative;
                    display: inline-block;
                    vertical-align: middle;

                    a {
                        position: relative;
                        display: inline-block;
                        vertical-align: top;
                        padding: 5px 0 10px;
                        margin: 0 37px;
                        font-size: 17px;
                        text-transform: uppercase;
                        font-weight: 600;
                        letter-spacing: 0.1em;
                        font-family: $default-heading-font;
                        color: $first-main-color;
                        transition: all .3s ease-in-out;
                        border-top: 4px solid transparent;

                        &:after {
                            content: "";
                            position: absolute;
                            top: -4px;
                            left: 0;
                            width: 100%;
                            height: 4px;
                            background-color: $first-main-color;
                            transform-origin: left;
                            transform: scaleX(0);
                            transition: .3s cubic-bezier(0.47, 0.11, 0, 0.96);
                        }

                        &:hover {
                            color: $default-heading-color;
                        }
                    }

                    &.active a,
                    &:hover a {
                        color: $default-heading-color;

                        &:after {
                            transform: scaleX(1);
                        }
                    }
                }
            }

            .entry-content {
                @include edgtfRelativeHolderLayout();

                > h2 {
                    display: none;
                }

                > p {
                    margin: 18px 0 20px;
                }
            }

            table {
                margin: 18px 0 20px;

                tr, th {
                    border: 0;
                }

                td, th {
                    padding: 3px 0;
                    vertical-align: middle;
                    text-align: left;

                    p {
                        margin: 0;
                    }
                }

                th {
                    color: $default-heading-color;
                    width: 1%;
                    padding: 0 10px 0 0;
                    font-family: Rajdhani,sans-serif;
                    font-weight: 700;
                    font-size: 20px;
                    text-transform: uppercase;
                }

                td {
                    width: 99%;
                    color: #fefefe;
                    font-family: "Open Sans",sans-serif;
                    font-size: 15px;
                    text-transform: none;
                }
            }

            #reviews {
                @include edgtfRelativeHolderLayout();
                margin: 18px 0 20px;

                h2 {
                    position: relative;
                    @include edgtfHeadingStyle(h3);
                    margin: 8px 0 24px;
                    padding: 0 0 0 22px;
                }

                ol.commentlist {
                    list-style: none;
                    margin-bottom: 35px;

                    li {
                        margin: 0 0 15px;
                        padding: 20px 20px;
                        background-color: #180f1f;

                        &:last-child {
                            margin: 0;
                        }
                    }

                    .comment_container {
                        @include edgtfRelativeHolderLayout();

                        img {
                            float: left;
                            width: 70px;
                        }
                    }

                    .comment-text {
                        padding: 0 0 0 90px;

                        p {
                            margin: 0;

                            .woocommerce-review__author {
                                font-family: $default-heading-font;
                                font-size: 18px;
                                font-weight: 700;
                                color: $default-heading-color;
                                text-transform: uppercase;
                            }
                        }
                    }
                }

                .comment-respond {
                    margin-top: 15px;

                    form {
                        .comment-form-comment,
                        .comment-form-author,
                        .comment-form-email {

                            textarea,
                            input {
                                background-color: #180f1f;
                                filter: none;
                            }
                        }

                        .comment-form-cookies-consent {
                            padding: 0 0 10px;
                        }
                    }

                    .comment-reply-title {
                        position: relative;
                        @include edgtfHeadingStyle(h3);
                        display: block;
                        margin: 0 0 12px;
                        padding: 0 0 0 22px;

                        &:before {
                            content: '';
                            position: absolute;
                            top: calc(50% - 2px);
                            left: 0;
                            display: block;
                            width: 3px;
                            height: 24px;
                            background-color: #5821a9;
                            transform: translateY(-50%);
                        }
                    }

                    .comment-notes {
                        @include edgtfHeadingStyle(h6);
                        margin: 4px 0 15px;
                    }

                    .comment-form-rating {
                        display: flex;
                        align-items: center;
                        margin: 0 0 20px;

                        label {
                            @include edgtfHeadingStyle(h5);
                            margin-right: 25px;
                        }
                    }

                    p {
                        margin: 0;
                    }

                    .stars {

                        > span {
                            position: relative;
                            display: flex;
                            margin: 10px 0;
                        }

                        &.selected {

                            a {
                                @include edgtfWooCommerceSingleProductRatingsStyle(true);
                            }
                        }

                        a {
                            position: relative;
                            display: block;
                            color: transparent;
                            margin: 0;
                            font-size: 0;
                            line-height: 1;
                            @include edgtfWooCommerceSingleProductRatingsStyle();
                        }
                    }
                }
            }
        }

        .related.products,
        .upsells.products {
            margin: 30px 0 0;

            > h2 {
                position: relative;
                display: block;
                @include edgtfHeadingStyle(h3);
                padding-left: 20px;
                margin: 66px 0 23px;
                text-align: left;

                &:before {
                    content: '';
                    position: absolute;
                    top: 5px;
                    left: 0;
                    display: block;
                    height: calc(100% - 11px);
                    width: 3px;
                    background-color: $first-main-color;
                }
            }

            .product {

                .edgtf-pl-text-wrapper {
                    margin-bottom: 0;
                }
            }
        }
    }
}

/* Woocommerce Product Page shortcode style - end
========================================================================== */