/* ==========================================================================
   WooCommerce Dropdown Cart styles - begin
   ========================================================================== */

.edgtf-shopping-cart-holder {
    position: relative;
    display: inline-block;
    vertical-align: top;
    height: 100%;
    margin: 0 10px;

    .touch & {
        cursor: pointer;
    }
    
    &:hover {
        
        .edgtf-sc-dropdown {
            opacity: 1;
            visibility: visible;
        }
    }

    .edgtf-shopping-cart-inner {
        @include edgtfRelativeHolderLayout();
        height: 100%;
    }

    .edgtf-header-cart {
        position: relative;
        display: flex;
        align-items: center;
        height: 100%;

        .edgtf-sc-opener-icon {
            position: relative;
            display: inline-block;
            vertical-align: top;
            line-height: 1;

            .edgtf-sc-opener-count {
                font-size: 13px;
                position: absolute;
                top: -5px;
                right: -9px;
            }

        }

        &.edgtf-header-cart-icon-pack {
            
            .edgtf-sc-opener-icon {
                font-size: 25px;
    
                * {
                    display: block;
                    line-height: inherit;
        
                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }

        &.edgtf-header-cart-svg-path {
            
            .edgtf-sc-opener-icon {

                > * {
                    vertical-align: middle;
                }
                
                > *, 
                path {
                    fill: currentColor;
                    stroke: currentColor;
                }
            }
        }
    }
}

.edgtf-dark-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-dark-color;

    &:hover {
        color: $header-dark-hover-color;
    }
}

.edgtf-light-header .edgtf-page-header > div:not(.edgtf-sticky-header):not(.fixed) .edgtf-shopping-cart-holder .edgtf-header-cart {
    color: $header-light-color;

    &:hover {
        color: $header-light-hover-color;
    }
}

.edgtf-sc-dropdown {
    position: absolute;
    top: 100%;
    left: auto;
    right: 0;
    text-align: left;
    box-sizing: border-box;
    transition: opacity .2s ease-out, visibility .2s ease-out;
    opacity: 0;
    visibility: hidden;
    
    .edgtf-sc-dropdown-inner {
        @include edgtfRelativeHolderLayout();
        width: 277px;
        padding: 38px 23px 28px;
        background-color: $transparent-background;
        box-shadow: $default-box-shadow;
        box-sizing: border-box;
    }
    
    .edgtf-sc-dropdown-items {
        @include edgtfRelativeHolderLayout();
    }
    
    .edgtf-sc-dropdown-item {
        position: relative;
        display: flex;
        margin-top: 16px;
        align-items: center;
        
        &:first-child {
            margin-top: 0;
        }
    }
    
    .edgtf-sc-dropdown-item-image {
        flex-shrink: 0;
        width: 60px;
        margin-right: 14px;
        
        a, img {
            display: block;
        }
    }
    
    .edgtf-sc-dropdown-item-content {
        @include edgtfRelativeHolderLayout();
        padding-right: 20px;
    }
    
    .edgtf-sc-dropdown-item-title {
        margin: 0;
        text-transform: uppercase;
    }

    .edgtf-sc-dropdown-item-quantity,
    .edgtf-sc-dropdown-item-separator,
    .edgtf-sc-dropdown-item-price {
        color: $first-main-color;
        display: inline-block;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 600;
    }
    
    .edgtf-sc-dropdown-item-remove {
        position: absolute;
        display: flex;
        top: -2px;
        right: 0;

    }
    
    .edgtf-sc-dropdown-subtotal {
        position: relative;
        margin-top: 20px;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 0;
        text-transform: uppercase;
        font-family: $default-heading-font;
        color: $default-heading-color;

        > * {
            display: inline-block;
            margin: 2px 0;
        }
    }
    
    .edgtf-sc-dropdown-button-holder {
        @include edgtfRelativeHolderLayout();
        margin-top: 10px;
    }
    
    .edgtf-sc-dropdown-button {
        font-size: 15px;
        i {
            margin: 0;
        }

        &.edgtf-btn-icon-left {
            i {
                margin: 0;
            }
        }

        &:nth-child(2) {
            float: right;
        }
    }
}

.edgtf-vertical-menu-area {

    .edgtf-sc-dropdown {
        display: none !important;
    }
}
/* ==========================================================================
   WooCommerce Dropdown Cart styles - end
   ========================================================================== */