/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommerceMarksGeneralStyle() {
	position: absolute;
	display: block;
	top: 0;
	width: auto;
	height: 30px;
	margin: 0;
	padding: 18px 0 0;
	font-family: $default-heading-font;
	color: $default-heading-color;
	font-weight: 700;
	font-size: 14px;
	line-height: 0.1em;
	text-align: center;
	z-index: 100;
	box-sizing: border-box;
	text-transform: uppercase;
	transform: rotate(45deg);

	&:after {
		content: "";
		position: absolute;
		border-top: 53px solid transparent;
		border-left: 53px solid transparent;
		top: 6px;
		right: -12px;
		transform: rotateZ(-45deg);
		width: 0;
		height: 0;
		z-index: -1;
	}
}

@mixin edgtfWooCommerceSaleStyle() {
	right: 0;
	
	&:after {
		border-top-color: $first-main-color;
	}
}

@mixin edgtfWooCommerceOutOfStockStyle() {
	right: 0;
	
	&:after {
		border-top-color: $default-heading-color;
	}
}

@mixin edgtfWooCommerceNewProductStyle() {
	right: 0;
	
	&:after {
		border-top-color: $second-main-color;
	}
}

/* ==========================================================================
   Marks (Sale, Out of Stock) mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Price mixin styles - begin
   ========================================================================== */

@mixin edgtfWooCommercePriceStyle() {
	position: relative;
	display: block;
	margin: 14px 0 0;
	line-height: 1;
	color: $default-heading-color;
	
	* {
		font-size: inherit;
		line-height: inherit;
	}
	
	del {
		margin: 0 8px 0 0;
		color: rgba($default-heading-color, .8);
	}
	
	ins {
		text-decoration: none;
	}
}

/* ==========================================================================
   Price mixin styles - end
   ========================================================================== */

/* ==========================================================================
   Star Ratings mixin styles - begin
   ========================================================================== */

%star-ratings {
	font-family: "ElegantIcons";
	font-size: 12px;
	line-height: 1;
	letter-spacing: 2px;
	color: $default-text-color;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

@mixin edgtfWooCommerceRatingsStyle() {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	@extend %star-ratings;
	overflow: hidden;
	
	&:before {
		content: "\e031\e031\e031\e031\e031";
	}
	
	span {
		position: absolute;
		top: 0;
		left: 0;
		overflow: hidden;
		
		&:before {
			content: "\e033\e033\e033\e033\e033";
		}
	}
}

@mixin edgtfWooCommerceSingleProductRatingsStyle($active-style: false) {
	$single-rating-default-icon: "\e031";
	$single-rating-active-icon: "\e033";
	
	@if ($active-style) {
		
		&.active {
			
			&:before {
				content: $single-rating-active-icon;
			}
			
			~ a {
				
				&:before {
					content: $single-rating-default-icon;
				}
			}
		}
		
		&:not(.active) {
			
			&:before {
				content: $single-rating-active-icon;
			}
		}
	} @else {
		
		&:before {
			content: $single-rating-default-icon;
			@extend %star-ratings;
		}
	}
}

/* ==========================================================================
   Star Ratings mixin styles - end
   ========================================================================== */