/* ==========================================================================
   WooCommerce Widgets styles - begin
   ========================================================================== */

.widget.woocommerce {
	clear: both;
	
	&.widget_rating_filter,
	&.widget_shopping_cart,
	&.widget_layered_nav,
	&.widget_layered_nav_filters,
	&.widget_product_categories,
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {
		background-color: $additional-background-color;
		padding: 23px 25px 38px;
		box-sizing: border-box;
		box-shadow: $default-box-shadow;
		
		ul {
			padding: 0;
			margin: 0;
			list-style: none;
			
			li {
				@include edgtfRelativeHolderLayout();
				padding: 0;
				margin: 0 0 14px;
				clear: both;
				
				&:last-child {
					margin: 0;
				}
				
				ul {
					margin: 14px 0 0 10px;
				}
			}
		}
	}
	
	&.widget_shopping_cart {
		
		.widget_shopping_cart_content {
			@include edgtfRelativeHolderLayout();
			
			ul {
				
				li {
					display: flex;
					flex-direction: column;
					justify-content: center;
					
					img {
						display: block;
						max-width: 70px;
						margin: 0 70% 13px 0;
					}
					
					.remove {
						position: absolute;
						top: 0;
						right: 0;
						font-size: 0;
						line-height: 1;
						
						&:after {
							position: relative;
							display: block;
							padding: 0 3px;
							font-family: 'Ionicons';
							content: "\f404";
							font-size: 30px;
							line-height: 25px;
						}
					}
				}
			}
			
			.variation {
				@include edgtfRelativeHolderLayout();
				
				> * {
					display: inline-block;
					vertical-align: middle;
					margin: 0;
					
					p {
						margin: 0;
					}
				}
			}
			
			.total {
				margin: 17px 0 13px;
			}
			
			.buttons {
				margin: 10px 0 0;
			}
		}
	}
	
	&.widget_layered_nav {
		
		.edgtf-widget-title {
			margin: 0 0 17px;
		}
		
		ul {
			
			li {
				margin: 2px 0;
				
				&.chosen {
					
					a {
						color: $first-main-color;
					}
				}
			}
		}
	}
	
	&.widget_layered_nav_filters {
		
		.edgtf-widget-title {
			margin: 0 0 17px;
		}
	}
	
	&.widget_product_categories,
	&.widget_layered_nav {
		
		ul {
			
			li {
				@include edgtfH5();
				font-family: $default-heading-font;
				color: $default-heading-color;
				font-weight: 700;
				line-height: 1.2em;
				text-transform: uppercase;
				
				a {
					position: relative;
					padding-left: 0;
					transition: color 0.2s ease-in-out, padding-left 0.2s ease-in-out;
					color: $default-heading-color;
					
					&:before {
						content: '';
						display: block;
						position: absolute;
						top: 12px;
						left: -20px;
						width: 12px;
						height: 2px;
						background-color: $first-main-color;
						opacity: 0;
						visibility: hidden;
						transition: left 0.3s ease-out, opacity 0.3s ease-out, visibility 0.3s ease-out;
					}
					
					&:hover {
						color: $first-main-color;
						padding-left: 20px;
						
						&:before {
							left: 0;
							color: $first-main-color;
							opacity: 1;
							visibility: visible;
							transition: left 0.2s ease-out 0.2s, opacity 0.2s ease-out 0.2s, visibility 0.2s ease-out 0.2s;
						}
					}
				}
				
				ul {
					margin: 0 0 0 10px;
				}
			}
		}
	}
	
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_recent_reviews,
	&.widget_top_rated_products {
		
		.edgtf-widget-title {
			margin: 0 0 29px;
		}
		
		ul {
			
			li {
				display: flex;
				flex-direction: column;
				justify-content: center;
				height: auto;
				
				a {
					
					.product-title {
						@include edgtfHeadingStyle(h4);
						
						&:hover {
							color: $first-main-color;
							transition: all 0.3s ease-in-out;
						}
					}
					
					img {
						position: absolute;
						top: 0;
						left: 0;
						width: 100%;
						max-width: 75px;
					}
					
					span {
						position: relative;
						display: block;
						padding: 0 0 0 102px;
						line-height: 20px;
						box-sizing: border-box;
					}
				}
				
				> *:not(a) {
					margin: 0 0 0 102px;
					line-height: 20px;
				}
				
				.woocommerce-Price-amount {
					font-family: $default-heading-font;
					color: $first-main-color;
					font-size: 14px;
					letter-spacing: 0.1em;
					padding-top: 4px;
				}
				
				.star-rating {
					position: relative;
					display: none;
					
					span {
						max-width: 100px;
					}
				}
				
				del,
				ins {
					display: block;
					
					span {
						color: inherit;
					}
				}

				del {
					color: rgba($first-main-color, .8);
				}

				ins {
					text-decoration: none;
				}
			}
		}
	}
	
	&.widget_products,
	&.widget_recently_viewed_products,
	&.widget_top_rated_products {
		
		ul {
			
			li {
				
				.product-title {
					color: inherit;
				}
				
				.amount {
					color: $first-main-color;
				}
			}
		}
	}
	
	&.widget_recently_viewed_products {
		
		ul {
			
			li {
				
				del,
				> .amount {
					display: block;
					margin-top: 10px;
				}
			}
		}
	}
	
	&.widget_recent_reviews {
		
		ul {
			
			li {
				
				.star-rating {
					margin-top: 7px;
					margin-bottom: 10px;
				}
			}
		}
		
		a {
			display: block;
			padding: 0 0 0 102px;
		}
		
		.reviewer {
			display: inline-block;
			vertical-align: middle;
			width: auto;
			clear: both;
		}
	}
	
	&.widget_top_rated_products {
		
		ul {
			
			li {
				
				.star-rating {
					margin-top: 10px;
					margin-bottom: 12px;
				}
				
				.amount {
					display: block;
				}
			}
		}
	}
	
	&.widget_product_tag_cloud {
		
		.tagcloud {
			
			a {
				position: relative;
				display: inline-block;
				vertical-align: middle;
				font-family: inherit;
				background-color: transparent;
				font-size: 14px !important;
				
				&:after {
					content: '/';
					color: $default-text-color;
					margin: 0 0 0 4px;
				}
				
				&:last-child {
					
					&:after {
						display: none;
					}
				}
			}
		}
	}
	
	&.widget_product_search {
		
		.woocommerce-product-search {
			@include edgtfRelativeHolderLayout();
			clear: both;
			
			> div {
				@include edgtfRelativeHolderLayout();
				border: 0;
				box-sizing: border-box;
				box-shadow: $default-box-shadow;
			}
			
			input,
			button {
				position: relative;
				display: inline-block;
				vertical-align: top;
				float: left;
				margin: 0;
				height: 63px;
				line-height: 24px;
				background-color: $additional-background-color;
				font-family: inherit;
				font-size: 15px;
				border: 0;
				border-radius: 0;
				outline: 0;
				-webkit-appearance: none;
				box-sizing: border-box;
				transition: color .2s ease-out;
			}
			
			input {
				width: calc(100% - 50px);
				padding: 10px 10px 10px 33px;
				color: $input-text-color;
				@include edgtfButtonClipPathTopLeft();
				
				@include edgtfPlaceholder {
					color: inherit;
				}
			}
			
			button {
				width: 50px;
				padding: 12px 31px 6px 0;
				cursor: pointer;
				color: $default-heading-color;
				@include edgtfButtonClipPathBottomRight();
				
				&:hover {
					color: $first-main-color;
				}
				
				span {
					display: block;
					line-height: inherit;
					
					&:before {
						display: block;
						line-height: inherit;
					}
				}
			}
		}
	}
}

.edgtf-top-bar,
.edgtf-page-header,
.edgtf-page-footer {

    .widget.woocommerce {

        &.widget_rating_filter,
		&.widget_shopping_cart,
		&.widget_layered_nav,
		&.widget_layered_nav_filters,
		&.widget_product_categories,
		&.widget_products,
		&.widget_recently_viewed_products,
		&.widget_recent_reviews,
		&.widget_top_rated_products {
            background-color: transparent;
            padding: 0;
            box-shadow: none;
        }
    }
}

/* ==========================================================================
   WooCommerce Widgets styles - end
   ========================================================================== */