/* ==========================================================================
   Product List shortcode styles - begin
   ========================================================================== */

.edgtf-pl-holder {
	@include edgtfRelativeHolderLayout();
	
	&.edgtf-info-on-image {
		
		.edgtf-pli {
			
			.edgtf-pli-inner {
				
				.edgtf-pli-text {
					@include edgtfAbsoluteHolderLayout();
					padding: 23px 21px;
					opacity: 1;
					background-color: transparent;
					box-sizing: border-box;
					transition: all .2s ease-out;

					.edgtf-pli-text-outer {
						text-align: left;
						
						.edgtf-pli-text-inner {
							display: flex;
							justify-content: center;
							align-items: center;
							width: 100%;
							height: 100%;

							.edgtf-pli-text-inner-2 {
								display: flex;
								align-items: flex-start;
								justify-content: flex-end;
								flex-direction: column;
								width: 100%;
								height: 100%;
							}

							.edgtf-pli-btn-inner {
								position: absolute;
								top: 0;
								left: 0;
								width: 100%;
								height: 100%;
								display: flex;
								align-items: center;
								justify-content: center;
							}

							.edgtf-pli-category {
								margin: 1px 0 0;
							}

							.edgtf-pli-add-to-cart {
								display: none;
							}
						}
					}
				}
				
				&:hover {
					
					.edgtf-pli-text {
						background-color: $default-overlay-color;
						transition: all .2s ease-out;
						
						.edgtf-pli-text-inner {

							.edgtf-pli-text-inner-2 {
								opacity: 0;
							}

							.edgtf-pli-add-to-cart {
								display: block;
							}
						}
					}
				}
			}
		}
	}
	
	.edgtf-pli {
		padding: 0;
		z-index: 100;
		
		.edgtf-pli-title {
			margin: 0 0 4px;
		}
		
		.edgtf-pli-category {
			margin: 5px 0 0;
			@include edgtfHeadingStyle(h6);
			font-family: $default-heading-font;
			text-transform: uppercase;
			
			a {
				color: $first-main-color !important;
				transition: .3s;

				&:hover {
					color: $second-main-color !important;
				}
			}
		}
		
		.edgtf-pli-excerpt {
			color: $default-heading-color;
			line-height: 1.3em;
			margin: 11px 0 3px;
		}
		
		.edgtf-pli-rating-holder {
			@include edgtfRelativeHolderLayout();
			line-height: 1;
			margin: 10px 0 3px;
		}
		
		.edgtf-pli-rating {
			@include edgtfWooCommerceRatingsStyle();
		}
		
		.edgtf-pli-price {
			@include edgtfWooCommercePriceStyle();
		}
	}
	
	.edgtf-pli-inner {
		position: relative;
		display: inline-block;
		vertical-align: middle;

		.edgtf-pli-text-inner {
			transform: translateY(10px);
			transition: .2s cubic-bezier(0.29, 0.1, 0, 0.98);
		}
		
		&:hover {
			
			.edgtf-pli-text {
				opacity: 1;

				.edgtf-pli-text-inner {
					transform: translateY(0);
				}
			}
		}
		
		.touch & {
			cursor: pointer;
		}
		
		.edgtf-pli-image {
			@include edgtfRelativeHolderLayout();
			box-shadow: $default-box-shadow;
			
			img {
				display: block;
				width: 100%;
			}
			
			.edgtf-pli-onsale,
			.edgtf-pli-out-of-stock,
			.edgtf-pli-new-product {
				@include edgtfWooCommerceMarksGeneralStyle();
			}
			
			.edgtf-pli-onsale {
				@include edgtfWooCommerceSaleStyle();
			}
			
			.edgtf-pli-out-of-stock {
				@include edgtfWooCommerceOutOfStockStyle();
				color: $transparent-background;
			}
			
			.edgtf-pli-new-product {
				@include edgtfWooCommerceNewProductStyle();
			}
		}
		
		.edgtf-pli-text {
			@include edgtfAbsoluteHolderLayout();
			padding: 10px;
			opacity: 0;
			background-color: $default-overlay-color;
			box-sizing: border-box;
			transition: opacity .2s ease-out;
		}
		
		.edgtf-pli-text-outer {
			position: relative;
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
			width: 100%;
			text-align: center;
		}
		
		.edgtf-pli-text-inner {
			position: relative;
			z-index: 12;
			
			.edgtf-pli-title,
			.edgtf-pli-category a,
			.edgtf-pli-excerpt,
			.edgtf-pli-rating,
			.edgtf-pli-price {
				color: #fff;
			}
			
			.edgtf-pli-category a:hover {
				color: rgba(#fff, .8);
			}
			
			.edgtf-pli-add-to-cart {

				@media (min-width: 1400px) {
						
					.edgtf-safari & {

						.edgtf-btn-bg-holder {
							top: 101%;
						}
					}
				}
				
				&.edgtf-default-skin {
					
					.button,
					.added_to_cart {
						@include edgtfButtonOutlineColor();
						@include edgtfButtonClipPathLines();
						padding: 13px 51px;

						@include laptop-landscape-large {
							padding: 10px 33px;
						}

						@include laptop-landscape-mac {
							padding: 10px 21px;
						}
						
						&:hover {
							@include edgtfButtonOutlineHoverColor();
						}
					}
				}
				
				&.edgtf-light-skin {
					
					.button,
					.added_to_cart {
						color: $default-heading-color;
						background-color: #fff;
						
						&:hover {
							color: #fff;
							background-color: $first-main-color;
						}
					}
				}
				
				&.edgtf-dark-skin {
					
					.button,
					.added_to_cart {
						color: #fff;
						background-color: $default-heading-color;
						
						&:hover {
							color: #fff;
							background-color: $first-main-color;
						}
					}
				}
			}
			
			.button,
			.added_to_cart {
				@include edgtfButtonDefaultStyle();
				@include edgtfButtonClipPathMain();
				padding: 13px 51px;
				cursor: pointer;
				z-index: 3;
				overflow: hidden;
			}
			
			.button.added {
				display: none;
			}
		}
		
		.edgtf-pli-link {
			@include edgtfAbsoluteHolderLayout();
		}
	}
	
	.edgtf-pli-text-wrapper {
		@include edgtfRelativeHolderLayout();
		
		.edgtf-pli-text-separat-wrapper {
			@include edgtfRelativeHolderLayout();
			margin: 23px 0 5px;
			display: flex;
			align-items: flex-start;
			justify-content: space-between;
			
			.edgtf-pli-price {
				@include edgtfWooCommercePriceStyle();
				@include edgtfHeadingStyle(h4);
				margin: 0;
				display: flex;
				flex-shrink: 1;
			}
		}
	}
	
	&.edgtf-product-info-light {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: #fff;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba(#fff, .8);
				}
			}
		}
	}
	
	&.edgtf-product-info-dark {
		
		.edgtf-pli-inner {
			
			.edgtf-pli-text-inner {
				
				.edgtf-pli-title,
				.edgtf-pli-category a,
				.edgtf-pli-excerpt,
				.edgtf-pli-rating,
				.edgtf-pli-price {
					color: $default-heading-color;
				}
				
				.edgtf-pli-category a:hover {
					color: rgba($default-heading-color, .8);
				}
			}
		}
	}
	
	/*************** Masonry Layout - begin ***************/
	
	&.edgtf-masonry-layout {
		
		.edgtf-pli-inner {
			width: 100%;
			height: 100%;
			
			.edgtf-pli-image {
				height: 100%;
			}
		}
	}
	
	/*************** Masonry Layout - end ***************/
}

/* ==========================================================================
   Product List shortcode styles - end
   ========================================================================== */