/* ==========================================================================
   WooCommerce Quantity Inputs styles - begin
   ========================================================================== */

.woocommerce-page .edgtf-content,
div.woocommerce {
	
	.edgtf-quantity-buttons {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		margin: 0 6px 0 0;
		background-color: $default-border-color;
		@include edgtfButtonClipPathBottomLeft();
		@include edgtfBoxShadows();

		.edgtf-quantity-input,
		.edgtf-quantity-minus,
		.edgtf-quantity-plus {
			display: inline-block;
			vertical-align: middle;
			color: $default-heading-color;
			font-size: 20px;
			font-weight: 700;
			background-color: transparent;
			border: none;
			text-align: center;
			-webkit-user-select: none;
			box-sizing: border-box;
			box-shadow: none;
		}
		
		.edgtf-quantity-input {
			position: relative;
			height: 64px;
			width: 173px;
			padding: 0;
			margin: 0 34px 0 0;
			font-family: $default-heading-font;
			line-height: 42px;
			outline: 0;
		}
		
		.edgtf-quantity-minus,
		.edgtf-quantity-plus {
			position: absolute;
			right: 0;
			width: 48px;
			height: 41px;
			line-height: 42px;
			cursor: pointer;
			transition: color .15s ease-in-out;
			
			&:hover {
				color: $first-main-color;
			}
		}
		
		.edgtf-quantity-plus {
			top: 0;
		}
		
		.edgtf-quantity-minus {
			bottom: 0;
		}
	}
}

.woocommerce-page .edgtf-content,
div.woocommerce {
	
	input[type="text"],
	input[type="email"],
	input[type="tel"],
	input[type="password"],
	textarea {
		@extend %input-style;
	}
	
	.coupon {
		
		input[type="text"] {
			width: 200px;
		}
	}
}

/* ==========================================================================
   WooCommerce Quantity Inputs styles - end
   ========================================================================== */